<template>
    <el-dialog
  :title="title"
  :visible.sync="dialogVisible"
  width="40%"
  center="center"
  :before-close="handleClose">
  <div class="op">
    <el-form :model="form" :rules="rules" ref="ruleForm" label-width="120px">
            <el-form-item label="楼栋" prop="buildingid">
                <div class="ol">
                    <el-select placeholder="请选择" v-model="form.buildingid">
                        <el-option v-for="(imet,index) in list" :key="index" :label="imet.name" :value="imet.id"></el-option>
                    </el-select>
                </div>
            </el-form-item>
        <el-form-item label="房间号" style="width:60%;" prop="houseno">
            <el-input v-model="form.houseno"></el-input>
        </el-form-item>
        <el-form-item label="房间面积(平米)" style="width:60%;" prop="areas">
            <el-input v-model="form.areas"></el-input>
        </el-form-item>
        <el-form-item label="房间类型">
                <div class="ol">
                    <el-select placeholder="请选择" style="width:35%;" v-model="sh">
                        <el-option label="1室" value="1室"></el-option>
                        <el-option label="2室" value="2室"></el-option>
                        <el-option label="3室" value="3室"></el-option>
                        <el-option label="4室" value="4室"></el-option>
                        <el-option label="5室" value="5室"></el-option>
                        <el-option label="6室" value="6室"></el-option>
                        <el-option label="7室" value="7室"></el-option>
                        <el-option label="8室" value="8室"></el-option>
                        <el-option label="9室" value="9室"></el-option>
                        <el-option label="10室" value="10室"></el-option>
                </el-select>
                <el-select placeholder="请选择" style="width:35%;" v-model="tin">
                        <el-option label="1厅" value="1厅"></el-option>
                        <el-option label="2厅" value="2厅"></el-option>
                        <el-option label="3厅" value="3厅"></el-option>
                        <el-option label="4厅" value="4厅"></el-option>
                        <el-option label="5厅" value="5厅"></el-option>
                        <el-option label="6厅" value="6厅"></el-option>
                        <el-option label="7厅" value="7厅"></el-option>
                        <el-option label="8厅" value="8厅"></el-option>
                        <el-option label="9厅" value="9厅"></el-option>
                        <el-option label="10厅" value="10厅"></el-option>
                </el-select>
                </div>
        </el-form-item>
        <el-form-item label="朝向" prop="orientation">
                <div class="ol">
                    <el-select placeholder="请选择" v-model="form.orientation">
                        <el-option label="朝东" value="朝东"></el-option>
                        <el-option label="朝西" value="朝西"></el-option>
                        <el-option label="朝南" value="朝南"></el-option>
                        <el-option label="朝北" value="朝北"></el-option>
                    </el-select>
                </div>
            </el-form-item>
    </el-form>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="serct">取 消</el-button>
    <el-button type="primary" @click="submit('ruleForm')">确 定</el-button>
  </span>
</el-dialog>
</template>

<script>
export default {
    name:'Houseadd',
    props:{
        dialogVisible:{
            type:Boolean,
            default:false
        },
        modcom:{
            type:Object,
            default:()=>{}
        }
    },
    data(){
        return{
            title:'添加房间',
            form:{},
            sh:'',
            tin:'',
            list:[],
            rules: {
                buildingid: [
                     { required: true, message: '请选择楼栋', trigger: 'change' }
                ],
                 houseno: [
                    { required: true, message: '请输入房间号', trigger: 'blur' },
                ],
                 areas: [
                    { required: true, message: '请输入房间面积', trigger: 'blur' },
                ],
                orientation: [
                     { required: true, message: '请选择朝向', trigger: 'change' }
                ],
            }
        }
    },
    created(){
        this.list = this.$store.state.building.buildingdata
    },
    updated(){
        // console.log(this.modcom.type)
        this.title = this.modcom.id?'修改楼栋':'添加楼栋'
        this.form = this.modcom?this.modcom:{}   //将值赋给query里面
        
        // this.form.buildingid = this.modcom.building
        // this.sh = this.modcom.type.substring(0,2)
        // this.tin = this.modcom.type.substring(2,4)
    },
    methods:{
        // 取消
        serct(){
            this.$emit('chansgedialogVisible',false)
        },
        // 确定
         submit(formName){

            this.form.type = this.sh += this.tin
            // console.log(this.form.building)

            this.$refs[formName].validate(async (valid) => {
            if (valid) {
                 let res = await this.http.post('/houses/add',this.form)
                if(res.errCode == 10000){ 
                    console.log(this.form.building)
                    this.$message.success('添加成功')
                    this.$emit('chansgedialogVisible',false)
                    this.sh = ""
                    this.tin = ""
                    this.$emit('chan')
                }
            } else {
                // console.log('error submit!!');
                return false;
            }
            });
        },
        // 添加
        handleClose(done) {
        this.$confirm("确认关闭？")
            .then(() => {
                this.$emit('chansgedialogVisible',false)
                this.sh = ""
                this.tin = ""
                this.$emit('chan')
            // done();
            })
            .catch(() => {});
        },
    }
}
</script>

<style lang="less" scoped>
.op{

    .el-form-item{
        margin-left: 60px;
        
        .ol{
            margin-left: 115px;
            
        }
        .el-input{
            margin-left: 115px;
            
        }
    }   
}
/deep/.el-form .el-input__inner{
                border: 1px solid #ccc !important;
            }
.el-form{
    display: block;
}
</style>