<template>
  <div>
    <el-card class="box-card">
      <span>房间管理</span>
    </el-card>
    <el-row>
      <div class="ol">
        <el-card class="box-card">
          <el-collapse accordion>
            <el-collapse-item>
              <template slot="title">
                <p><i class="el-icon-search"></i>筛选查询</p>
              </template>
              <el-row>
                <el-col :span="24">
                  <el-form ref="query" :model="query" label-width="110px">
                    <el-form-item label="楼栋ID：" style="width: 30%">
                      <el-select v-model.trim="query.buildingid" placeholder="请选择" @change="seratch" clearable @keyup.native.enter="seratch">
                        <el-option v-for="imet in list" :key="imet.id" :label="imet.name" :value="imet.id"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="房间号：" style="width: 30%">
                      <el-input v-model="query.houseno" @change="seratch" clearable @keyup.native.enter="seratch"></el-input>
                    </el-form-item>
                    <el-form-item label="房间类型：" style="width: 30%">
                      <el-input v-model="query.type"></el-input>
                    </el-form-item>
                    <el-form-item
                      label="房间面积范围："
                      v-model="query.areas"
                      style="margin-left: 40px; padding-right: 0px"
                      @change="seratch" clearable @keyup.native.enter="seratch"
                    >
                      <el-input
                        v-model="query.max"
                        placeholder="最大"
                        style="width: 35%"
                      ></el-input
                      >——
                      <el-input
                        v-model="query.min"
                        placeholder="最小"
                        style="width: 35%"
                      ></el-input>
                    </el-form-item>
                    <el-form-item label="房间朝向：" style="width: 30%">
                      <el-select
                        v-model="query.orientation"
                        placeholder="请选择"
                        @change="seratch" clearable @keyup.native.enter="seratch">
                         <el-option label="朝东" value="朝东"></el-option>
                        <el-option label="朝西" value="朝西"></el-option>
                        <el-option label="朝南" value="朝南"></el-option>
                        <el-option label="朝北" value="朝北"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item>
                      <el-button type="primary" @click="seratch">查询</el-button>
                    </el-form-item>
                  </el-form>
                </el-col>
              </el-row>
            </el-collapse-item>
          </el-collapse>
          <div class="ul">
            <p><i class="el-icon-s-grid"></i>数据列表</p>
            <el-button type="primary" @click="dialogVisible = true"
              >添加</el-button
            >
            <el-button type="danger" @click="del">批量删除</el-button>
          </div>
        </el-card>
      </div>
    </el-row>
    <!-- 表格 -->
    <Table
      :loading="loading"
      :tableData="tableData"
      :tableCouml="tableCouml"
      @idshow="idshow"
    >
      <template #option="data">
        <el-button size="mini" type="success" @click="mod(data.data)"
          >修改</el-button
        >
        <el-button size="mini" type="danger" @click="handleDelete(data.data)"
          >删除</el-button
        >
      </template>
    </Table>

    <!-- 分页 -->
    <Paging :query="query" :total="total" @paging="paging"></Paging>

    <!-- 添加 -->
    <HouseAdd
      :dialogVisible="dialogVisible"
      @chansgedialogVisible="chansgedialogVisible"
      @chan="chan"
      :modcom="modcom"
    ></HouseAdd>
  </div>
</template>

<script>
import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";
import HouseAdd from "@/components/House/HouseAdd";

export default {
  data() {
    return {
      loading: true,
      form: {},
      tableData: [],
      tableCouml: [
        { prop: "id", label: "房间ID", width: "180" },
        { prop: "building", label: "楼号", width: "200" },
        { prop: "houseno", label: "房间号", width: "180" },
        { prop: "areas", label: "面积(平米)", width: "180" },
        { prop: "type", label: "房间类型", width: "180" },
        { prop: "orientation", label: "朝向", width: "180" },
        { label: "操作", isshow: true, showname: "option" },
      ],
      total: 0,
      query: {
        page: 1,
        psize: 5,
      },
      dialogVisible: false,
      ids: [],
      modcom:{},
      list:[],
    };
  },
  components: {
    Table,
    Paging,
    HouseAdd,
  },
  created() {
    this.getimet();
    this.list = this.$store.state.building.buildingdata
  },
  methods: {
    // 刷新页面
    paging() {
      this.getimet();
    },
    // 清空
    chan() {
      this.modcom = {};
    },
    // 修改
    mod(val){
      this.modcom = JSON.parse(JSON.stringify(val))
      this.dialogVisible = true
    //   this.modcom = {}
    },

    // 批量删除
    idshow(id) {
      this.ids.push(id);
    },
    del() {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          let res = await this.http.post("/houses/deleteall", {
            ids: this.ids,
          });
          if (res.errCode == 10000) {
            this.$message.success("删除成功");
            this.getimet();
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 删除
    handleDelete(val) {
    //   console.log(val);
         this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async () => {
              let res = await this.http.get('/houses/delete',{id:val.id})
              if(res.errCode == '10000'){
                  this.$message.success('删除成功')
                  this.getimet()
              }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
    },
    // // 传递
    // chansgedialogVisible(val) {
    //   this.dialogVisible = val;
    //   this.getimet();
    // },
    //查询
    seratch() {
      this.getimet();
    },

    // 列表
    async getimet() {
      let res = await this.http.get("/houses/list", this.query);
      // console.log(res)
      if (res.errCode == "10000") {
        this.tableData = res.data.list;
        this.total = res.data.counts;
        this.loading = false;
      }
    },
    // 添加
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    chansgedialogVisible(val) {
      this.dialogVisible = val;
      this.getimet();
    },
  },
};
</script>

<style lang="less" scoped>
@import "../../assets/css/admin.css";
/deep/.el-collapse-item__header {
  font-size: 14px !important;
  font-weight: bold !important;
  background-color: transparent;
  border: none !important;
  // i{
  //     font-size: 14px !important;
  //     font-weight: bold !important;
  // }
}
.el-collapse {
  border: 0px;
}
/deep/.el-collapse-item__wrap {
  background-color: transparent !important;
  border: 0px;
}
.ol {
  /deep/.el-card__body {
    padding: 0px !important;
  }
  p {
    margin-left: 20px;
    i {
      margin-right: 10px;
    }
  }
  .ul {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    margin-top: 10px;
    p {
      color: darkgray;
    }
  }
}

.el-button--primary {
  margin-left: 15px;
}
/deep/.el-button {
  margin-right: 11px !important;
}
.el-row {
  margin-top: 20px;
}
.el-form {
  margin-top: 0px;
  padding: 0px 15px;
}
/deep/.el-collapse-item__content {
  padding: 0px !important;
}
</style>